import React, { Component } from "react";
import { moviesApi, tvApi } from "../../api";
import SearchPresenter from "./SearchPresenter";

export default class extends Component {
  state = {
    movieResults: null,
    tvResults: null,
    searchTerm: "",
    // ì‚¬ìš©ìê°€ ê²€ìƒ‰ì„ ì‹œë„í–ˆì„ë•Œ, ì˜í™”, í‹°ë¹„ ê²°ê³¼ë¥¼ ë‘˜ ë‹¤ ë³´ì—¬ì¤„ ê²ƒì´ê¸° ë•Œë¬¸ì—
    // lodingì€ falseë¡œ ì´ˆê¸°í™”ë¥¼ ì‹œì¼œë‘”ë‹¤.
    loading: false,
    error: null
  };

  //   handleSubmit
  handleSubmit = (event) => {
    // submitì„ í–ˆì„ë•Œ, browserê°€ pageê°€ ìƒˆë¡œê³ ì¹¨ë˜ë©´ì„œ
    // ì›ë˜ ê°–ê³  ìˆë˜ stateì •ë³´ë¥¼ refreshí•˜ëŠ” ê²ƒì„ ë°©ì§€í•˜ê¸° ìœ„í•´
    event.preventDefault();
    const { searchTerm } = this.state;
    if (searchTerm !== "") {
      this.searchByTerm();
    }
  };

  // UpdateSearch
  updateTerm = (event) => {
    const {
      target: { value }
    } = event;
    // stateê°’ì„ ì—…ë°ì´íŠ¸ í•´ì£¼ë©´ í˜ì´ì§€ë„ ìë™ìœ¼ë¡œ re-renderingë˜ê¸° ë•Œë¬¸ì—
    // ì•„ë˜ì™€ ê°™ì´ state ë‚´ë¶€ì— ìˆëŠ” searchTermí•­ëª©ì„ ì…ë ¥ë°›ì€ valueê°’ìœ¼ë¡œ ì´ˆê¸°í™”ë¥¼ ì‹œì¼œì¤€ë‹¤.
    this.setState({
      searchTerm: value
    });
  };

  searchByTerm = async () => {
    const { searchTerm } = this.state;
    this.setState({ loading: true });
    try {
      const {
        data: { results: movieResults }
      } = await moviesApi.search(searchTerm);
      const {
        data: { results: tvResults }
      } = await tvApi.search(searchTerm);
      // ëˆ„êµ°ê°€ ê²€ìƒ‰ì„ í–ˆì„ë•Œ, ë¡œë”©ì„ trueë¡œ ë§Œë“ ë‹¤. (default:false)
      this.setState({
        movieResults,
        tvResults
      });
    } catch {
      this.setState({ error: "Can't find results." });
    } finally {
      this.setState({ loading: false });
    }
  };

  render() {
    const { movieResults, tvResults, searchTerm, loading, error } = this.state;
    return (
      <SearchPresenter
        movieResults={movieResults}
        tvResults={tvResults}
        loading={loading}
        error={error}
        searchTerm={searchTerm}
        handleSubmit={this.handleSubmit}
        updateTerm={this.updateTerm}
      />
    );
  }
}
