import React from "react";
import PropTypes from "prop-types";
import styled from "styled-components";
import Section from "Components/Section";
import Loader from "../../Components/Loader";
import Message from "Components/Message";
import Poster from "../../Components/Poster";

const Container = styled.div`
  padding: 20px 20px;
`;

// loading ì¤‘ì´ë©´ í™”ë©´ì— í‘œì‹œí•´ì¤„ nowPlaying ê°ì²´ê°€ nullì´ê¸° ë•Œë¬¸ì— loadingì´ falseì¼ë•Œ Containerë¥¼ ì™„ì„±ì‹œí‚¨ë‹¤.
const HomePresenter = ({ nowPlaying, popular, upcoming, loading, error }) =>
  loading ? (
    <Loader />
  ) : (
    <Container>
      {/* loadingí• ë•Œì—ëŠ” nowPlaying ê°’ì€ ì¡°ì¬í•˜ì§€ ì•ŠëŠ”ë‹¤. */}
      {/* nowPlayingì´ ì¡´ì¬í•˜ëŠ”ì§€ ì²´í¬ë¥¼ í•´ì¤€ë‹¤. nowPlayingì´ ì¡´ì¬í•˜ê³  ê¸¸ì´ê°€ 0ë³´ë‹¤ í¬ë‹¤ë©´ Section Componentë¥¼ ì™„ì„±ì‹œí‚¨ë‹¤. */}
      {nowPlaying && nowPlaying.length > 0 && (
        <Section title="Now Playing">
          {nowPlaying.map((movie) => (
            <Poster
              key={movie.id}
              id={movie.id}
              imageUrl={movie.poster_path}
              title={movie.original_title}
              rating={movie.vote_average}
              year={movie.release_date.substring(0, 4)}
              isMovie={true}
            />
          ))}
        </Section>
      )}
      {upcoming && upcoming.length > 0 && (
        <Section title="Upcoming Movies">
          {upcoming.map((movie) => (
            <Poster
              key={movie.id}
              id={movie.id}
              imageUrl={movie.poster_path}
              title={movie.original_title}
              rating={movie.vote_average}
              year={movie.release_date.substring(0, 4)}
              isMovie={true}
            />
          ))}
        </Section>
      )}
      {popular && popular.length > 0 && (
        <Section title="Popular Movies">
          {popular.map((movie) => (
            <Poster
              key={movie.id}
              id={movie.id}
              imageUrl={movie.poster_path}
              title={movie.original_title}
              rating={movie.vote_average}
              year={movie.release_date.substring(0, 4)}
              isMovie={true}
            />
          ))}
        </Section>
      )}
      {error && <Message text={error} color="#e74c3c" />}
    </Container>
  );

HomePresenter.propTypes = {
  nowPlaying: PropTypes.array,
  popular: PropTypes.array,
  upcoming: PropTypes.array,
  loading: PropTypes.bool.isRequired,
  error: PropTypes.string
};

export default HomePresenter;
